import { Component, EventEmitter, Input, OnChanges, OnInit, Output } from '@angular/core';
import { FormGroup, FormBuilder } from '@angular/forms';

import { FieldConfig } from '../../models/field-config.interface';

@Component({
  exportAs: 'dynamicForm',
  selector: 'dynamic-form',
  styleUrls: ['dynamic-form.component.scss'],
  template: `
    <form
      class="dynamic-form"
      [formGroup]="form"
      (submit)="handleSubmit($event)">
      <ng-container
        *ngFor="let field of config;"
        dynamicField
        [config]="field"
        [group]="form">
      </ng-container>
    </form>
  `
})
export class DynamicFormComponent implements OnChanges, OnInit {
  @Input()
  config: FieldConfig[] = [];

  @Output()
  submit: EventEmitter<any> = new EventEmitter<any>();

  form: FormGroup;

  get controls() { return this.config.filter(({type}) => type !== 'button'); }
  get changes() { return this.form.valueChanges; }
  get valid() { return this.form.valid; }
  get value() { return this.form.value; }

  constructor(private builder: FormBuilder) {}

  ngOnInit() {
    this.form = this.createGroup();
  }

  ngOnChanges() {
    if (this.form) {
      const currentControls = Object.keys(this.form.controls);
      const configControls = this.controls.map((item) => item.name);

      const toRemove = currentControls.filter((control) => !configControls.includes(control));
      const toAdd = configControls.filter((control) => !currentControls.includes(control));

      for (let i = 0; i < toAdd.length; i++) {
        const name = toAdd[i];
        const config = this.config.find((control) => control.name === name);
        this.form.addControl(toAdd[i], this.createControl(config));
      }

      for (let i = 0; i < toRemove.length; i++) {
        this.form.removeControl(toRemove[i]);
      }
    }
  }

  createGroup() {
    const group = this.builder.group({});
    const controls = this.controls;

    for (let i = 0; i < controls.length; i++) {
      group.addControl(controls[i].name, this.createControl(controls[i]));
    }

    return group;
  }

  createControl(config: FieldConfig) {
    const { disabled, validation, value } = config;

    return this.builder.control({ disabled, value }, validation);
  }

  handleSubmit(event: Event) {
    event.preventDefault();
    event.stopPropagation();
    this.submit.emit(this.value);
  }

  setDisabled(name: string, disable: boolean) {
    if (this.form.controls[name]) {
      const method = disable ? 'disable': 'enable';
      this.form.controls[name][method]();
      return;
    }

    this.config = this.config.map((item) => {
      if (item.name === name) {
        item.disabled = disable;
      }
      return item;
    });
  }

  setValue(name: string, value: any) {
    this.form.controls[name].setValue(value, {emitEvent: true});
  }
}



